@use 'base/mixins' as m;
@use 'base/variables' as v;


@media(prefers-color-scheme: dark) {
    $grisOscuro: #2c2d3b;
    body {
        background-color: $grisOscuro;
    }
    
    .tagline, 
    .formulario label,
    .login .acciones a,
    .crear .acciones a,
    .olvide .acciones a {
        color: v.$blanco;
    }

    .descripcion-pagina,
    .formulario .campo input,
    .nombre-pagina{
        color: v.$grisClaro;
    }

    .formulario .campo input {
        background-color: lighten($grisOscuro, 15%) !important; 
        &::placeholder {
            color: darken($color: v.$grisClaro, $amount: 40%);
        }
    }

    .alerta.error {
        background-color: v.$blanco;
    }

    .sidebar {
        @include m.tablet {
            background-color: darken($color: $grisOscuro, $amount: 5%);
        }

        .sidebar-nav a {    
            &.activo,
            &:hover {
                background-color: v.$naranja;
            }
        }

    }
    
    .barra-mobile,
    .barra {
        background-color: darken($grisOscuro, 10%);
    }

    .barra {
        p {
            color: v.$grisClaro;
        }

        span {
            color: v.$naranja;
        }

        .cerrar-sesion {
            @include m.boton(v.$naranja, v.$blanco);
            padding: 1rem;
            font-size: 1.2rem;
        }


    }

    .filtros {
        background-color: lighten($grisOscuro, 10%);

        h2, label {
            color: v.$blanco;
        }

        input[type="radio"] {
            accent-color: v.$cyan;
        }
    }

    .dashboard {
        background-color: $grisOscuro;

        .enlace  {
            color: v.$grisClaro;
            &:hover {
                color: darken(v.$grisClaro, 20%);
            }
        }
    }

    .listado-proyectos .proyecto {
        background-color: lighten($grisOscuro, 20%);

        &:hover {
            background-color: lighten($grisOscuro, 10%);
        }
    }

    .listado-tareas .tarea {
        background-color: lighten($grisOscuro, $amount: 10%);
        border: none;
        p {
            color: v.$grisClaro;
        }
    }

    .modal .formulario {
        background-color: lighten($grisOscuro, $amount: 10%);

        legend {
            color: v.$grisClaro;
        }

        .campo input {
            background-color: lighten($color: $grisOscuro, $amount: 20%) !important;
            &::placeholder {
                color: darken($color: v.$grisClaro, $amount: 40%);
            }
        }
    }
}
