@use 'base/mixins' as m;
@use 'base/variables' as v;

.filtros {
    background-color: v.$blanco;
    margin-top: 2rem;
    border-radius: 1rem;
    padding: 2rem;
    -webkit-box-shadow: 0px 3px 5px 1px rgba(0,0,0,0.26);
    -moz-box-shadow: 0px 3px 5px 1px rgba(0,0,0,0.26);
    box-shadow: 0px 3px 5px 1px rgba(0,0,0,0.26);

    .filtros-inputs {
        @include m.tablet{
            display: flex;
            align-items: center;
            justify-content: space-around;
            gap: 2rem;
        }
    }

    h2 {
        font-size: 2rem;
        color: v.$indigo;
        @include m.tablet{
           margin: 0;
        }
    }

    .campo {
        margin-bottom: 2rem;
        display: flex;
        justify-content: space-between;
        gap: 1rem;
        align-items: center;
        @include m.tablet {
            margin-bottom: 0;
        }
    }

    input[type="radio"] {
        accent-color: v.$indigo;
        width: 1.4rem;
        height: 1.4rem;
    }
}