@use 'base/mixins' as m;
@use 'base/variables' as v;

.sidebar {
    opacity: 0;
    background-color: v.$indigo;
    height: 0;
    overflow: hidden;
    backdrop-filter: none; 
    transition-property: opacity, backdrop-filter;
    transition-duration: 300ms;

 

    &.mostrar {
        opacity: 1;
        padding: 2rem;
        height: 100%;
        backdrop-filter: blur(8px); 
        background-color: rgba(0, 0, 0, .85);
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        @include m.tablet {
            display: none;
        }
    }

    &.ocultar {
        opacity: 0;
    }

    @include m.tablet {
        opacity: 1;
        height: 100%;
        padding: 2rem;
    }

    .contenedor-sidebar {
        display: flex;
        justify-content: space-between;
        align-items: center;

        h2 {
            color: v.$blanco;
            margin: 0;
            text-align: center;
        }

        .cerrar-menu{
            display: flex;
            justify-content: center;
            height: 2rem;
            width: 2rem;
            @include m.tablet {
                display: none;
            }

            &:hover {
                cursor: pointer;
            }
        }
    }

    .sidebar-nav {
        margin-top: 5rem;

        a {
            color: v.$blanco;
            display: block;
            margin-bottom: 2rem;
            text-align: center;
            padding: .7rem;
            transition-property: background-color;
            transition-duration: .3s;
            font-size: 2rem;

            &:hover,
            &.activo {
                background-color: darken(v.$indigo, 20%);
                border-radius: .5rem;
            }

            @include m.tablet {
                font-size: 1.5rem;
                margin-bottom: 1.5rem;
                text-align: left;
            }
        }

    }

    .cerrar-sesion-mobile {
        margin-top: 10rem;
        .cerrar-sesion {
            @include m.boton(v.$indigo, v.$blanco)
        }

        @include m.tablet {
            display: none;
        }
    }
}